html {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

#root {
  width: 100%;
  min-height: 100%;
  height: 100%;
}


.register-form {
  background-color: #eef2f5;
  border-radius: 6px;
  width: 480px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px 50px;
  box-sizing: border-box;
}

.register-header {
  color:#384556;
  font-size: 32px;
  margin-bottom: 20px;
}

.register-container {
  width: 100%;
}

.register-block {
  padding: 5px 0;
}

.register-block-group {
  padding: 5px 0;
}

.register-block-header {
  font-size: 14px;
  color: #97989a;
}

.tooltip {
  font-size: 10px;
  color: #97989a;
}

input[type='text'], input[type='password'], input[type='email'] {
  width: 100%;
  outline: none;
  background-color: #dde3ef;
  border: 1px solid transparent;
  padding: 16px;
  font-size: 18px;
  color: #6f8092;
  border-radius: 3px;
  transition: all 0.35s ease-in-out;
}

.register-form-buttons {
  margin-bottom: 5px;
  margin-top: 10px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

.register-submit-button {
  border: none;
  height: 53px;
  width: 120px;
  line-height: 53px;
  background-color: #59ace0;
  font-size: 18px;
  text-transform: uppercase;
  color: #ffffff;
  border-radius: 3px;
  font-weight: bold;
  cursor: pointer;
  outline: none;
}

.register-back-button {
  border: none;
  height: 53px;
  width: 120px;
  line-height: 53px;
  background-color: #ffffff;
  font-size: 18px;
  text-transform: uppercase;
  color: #6f8092;
  border-radius: 3px;
  font-weight: bold;
  cursor: pointer;
  outline: none;
}

.input-container .correct {
  border: 1px solid #36bfb5;
}

.input-container .incorrect {
  border: 1px solid #f05656;
}

.authenticate-form {
  position: relative;
  background-color: #eef2f5;
  border-radius: 6px;
  width: 480px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px 50px 75px;
  box-sizing: border-box;
}

.authenticate-header {
  color:#384556;
  font-size: 32px;
  margin-bottom: 20px;
}

.authenticate-container {
  width: 100%;
}

.authenticate-block {
  padding: 5px 0;
}

.authenticate-block-group {
  padding: 5px 0;
}

.authenticate-form-buttons {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.authenticate-submit-button {
  border: none;
  height: 53px;
  width: 120px;
  line-height: 53px;
  background-color: #59ace0;
  font-size: 18px;
  text-transform: uppercase;
  color: #ffffff;
  border-radius: 3px;
  font-weight: bold;
  cursor: pointer;
  outline: none;
}

.redirect-to-register {
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: -1px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  background-color: #495972;
  border-radius: 0 0 6px 6px;
  color: #d6dee9;
  text-decoration: none;
}
.dashboard {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #eef2f5;
  width: 549px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  border-radius: 6px;
}

.dashboard-content {
  display: flex;
  height: 238px;
}

.dashboard-photo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40%;
}

.dashboard-user-photo {
  display: flex;
  align-items: center;
  width: 128px;
  height: 128px;
  background-image: url(/static/media/userIcon.5b8e418c.png);
  background-size: cover;
  background-position: center center;
}

.dashboard-info {
  display: flex;
  flex-direction: column;
}

.dashboard-username {
  font-size: 28px;
  margin-top: 47px;
  margin-bottom: 15px;
}

.dashboard-info-basic div {
  margin-bottom: 5px;
  letter-spacing: 1px;
}

.dashboard-footer {
  height: 78px;
  background-color: #11ac63;
  border-radius: 0 0 6px 6px;
}

.dashboard-logout-button {
  display: block;
  height: 100%;
  text-align: center;
  line-height: 78px;
  color: #eef2f5;
  text-decoration: none;
  font-size: 22px;
  cursor: pointer;
}
.App {
  min-height: 100%;
  background-color:#384556;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-buttons {
  display: flex;
  
  justify-content: space-between;
}

.authenticate-button {
  border: 2px solid #e8ecef;
  border-radius: 4px;
  width: 100px;
  height: 50px;
  margin-right: 10px;
}

.register-button {
  border: 2px solid #e8ecef;
  border-radius: 4px;
  width: 100px;
  height: 50px;
}

.authenticate, .register {
  text-decoration: none;
  color: #e8ecef;
  display: block;
  line-height: 50px;
  text-align: center;
}

.authenticate-button:hover, .register-button:hover{
  background-color: #e8ecef;
}

.authenticate-button:hover .authenticate,  .register-button:hover .register{
  color: #384556;
}
